const factorialCache: Map<string, string[][]> = new Map();

/**
 * Возвращает ряд факториалов от 0 до n.
 * @customfunction FACTORIALROW
 * @param {number} n Число, до которого считать факториалы
 * @returns {Promise<string[][]>} Ряд факториалов от 0 до n (в виде вертикального массива)
 */
export async function factorialRow(n: number): Promise<string[][]> {
    console.log("Входное значение:", n);

    if (n < 0 || !Number.isInteger(n)) {
      console.error("Ошибка: введено отрицательное или нецелое число");
      throw new Error("Input must be a non-negative integer.");
    }
    else if (n > 500) {
      console.error("Ошибка: введено число больше 500");
      throw new Error("Input must be less then 500.");
    }

    const mode: string | null = await localStorage.getItem('orientationMode');
    console.log("Режим из storage:", mode);
    // Создание ключа из n и mode
    const cacheKey = `${n}_${mode}`;

    let result: string[][];

    // Если значение уже вычислено, возвращаем его из кеша
    if (factorialCache.has(cacheKey)) {
      result = factorialCache.get(cacheKey)!;
      console.log("Результат из factorialCache:", result);
    }
    else {
      const resultArray: string[] = [];
      let factorial: bigint = BigInt(1);

      for (let i = 1; i <= n; i++) {
        factorial *= BigInt(i);
        resultArray.push(factorial.toString());
      }

      if (mode === 'row') {
        result = [resultArray]; // строка: одна строка, много колонок
      }
      else {
        // колонка: много строк, одна колонка
        result = resultArray.map(val => [val]);
      }

      factorialCache.set(cacheKey, result);

      console.log("Результат:", result);
    }

    return result;
}
CustomFunctions.associate("FACTORIALROW", factorialRow);